  </div>
</template>

<script>

/////////////////
import HelloWorld from './components/HelloWorld'
//import Imagen from './components/Image'
//import Galeria from './components/Galeria'
import spritespin from './components/spritespin.vue'
//import img from './assets/bike6x6_big.jpg';
var img = require('./assets/Elefante13d.jpg');
//import img from './assets/Elefante13d.jpg';

export default {
  name: 'App',
  components: {
   //Galeria,
   spritespin,
  //  Imagen
  HelloWorld,
 
  },
  data: function () {


    // return{
    //   show:true,
    //   options:{
    //   source: SpriteSpin.sourceArray('https://spritespin.ginie.eu/images/rad_zoom_{frame}.jpg', { frame: [1,34], digits: 3 })
    //   },
    // }
      
      return {
     
        show: true,
        options: {
        source: img,
        width: 480,
        height: 327,
        frames: 23,
        framesX: 6,
        sense: -1,
        animate: false,
        },
      }
    },
    methods: {


      toggleShow: function() {
        this.show = !this.show
      },
      prev: function() {
        if (this.$refs.spritespin) {
          this.$refs.spritespin.api.stopAnimation()
          this.$refs.spritespin.api.prevFrame()
        }
      },
      next: function() {
        if (this.$refs.spritespin) {
          this.$refs.spritespin.api.stopAnimation()
          this.$refs.spritespin.api.nextFrame()
        }
      },
      togglePlayback: function() {
        if (this.$refs.spritespin) {
          this.$refs.spritespin.api.toggleAnimation()
        }
      },
      toggleFullscreen: function() {
        if (this.$refs.spritespin) {
          this.$refs.spritespin.api.requestFullscreen()
        }
      },
    }
}
</script>

