<template>

<div></div>
  
</template>

<script>
const $ = require('jquery')
window.$ = $
var spritespin = require("spritespin")
export default {
    name: 'spritespin',
    props: ['options'],
    data: function () {
      return {
        api: null,
        data: null,
      }
    },
     mounted: function() {
         console.log(spritespin)
      // create spritespin
      $(this.$el).spritespin(this.options)
      // access api object
      this.api = $(this.$el).spritespin('api')
      // access data object
      this.data = $(this.$el).spritespin('data')
      // watch changes and update spritespin
      this.$watch('options', (newVal, oldVal) => {
          console.log(oldVal)
        $(this.$el).spritespin(newVal)
      })
    },
    updated: function() {
      // $(this.$el).spritespin(this.options)
    },
    beforeDestroy: function() {
      // destroy spritespin before Vue node is destroyed
      $(this.$el).spritespin('destroy')
    }

}
</script>

